<template>
  <div class="tab-wrapper">
    <ul class="nav nav-tabs tab-wrapper-inner">
      <li class="nav-item" v-for="(item, index) in tablist" :class="{active: activeTab(index)}" @click="activateTab(index);">
        <a class="nav-link" href="#" @click="enableTab(item.tabname)">{{ item.productname}}</a>
      </li>
    </ul>

    <div class="item-tab" v-for="(item, index) in tablist" v-show="currentTab(item.tabname)">

      <div class="item-wrapper">
        <div class="item-details" v-html="item.tabmsg">
        </div>
        <div class="sub-menu">
          <div class="menu-item" v-for="(brand_name, index) in item.brand" :class="{active: currentBrand(index)}" @click="item.picked_brand = brand_name.name; item.current_img = 99; activateBrand(index);showProjectDescription(brand_name.description);showProjectUrl(brand_name.url);showProjectWw(brand_name.password)">{{brand_name.name}}</div>
        </div>

        <div class="item-details-footer">
			<div class="item-details-description details-footer-field">
				{{projectOmschrijving}}
			</div>
			<div class="item-details-url details-footer-field" v-show="projectUrl !='' ">
				{{projectWw}}
			</div>
			<div class="item-details-url details-footer-field" v-show="projectUrl !='' ">
				<a class="project-url" target="_blank" v-bind:href="projectUrl">{{projectUrl}}</a>


			</div>
        </div>
      </div>
      <div class="item_images" v-for="b in item.brand" v-show ="b.name == item.picked_brand">
        <div class="item_images_thumb">
          <div class="thumb-image" v-for="(thumb, index) in item.item_images" :class="{active: activeThumb(index)}" @click="item.current_img = index + 1; activateThumb(index);" v-show ="thumb.brand == item.picked_brand">
            {{thumb.view}}
            <img :src=thumb.item_img  />
          </div>
        </div>
        <div v-for="(i_img, index) in item.item_images" class="big-image" v-show="item.current_img == index + 1" @click="showScreenshot(i_img.item_img)">
          <img :src=i_img.item_img />
          <div class="big-image-icon"><i class="fas fa-expand-arrows-alt"></i></div>

        </div>
      </div>
    </div>

    <!-- MODAL -->
    <div class="modal fade" id="screenshot-modal" tabindex="-1" role="dialog" aria-labelledby="screenshot-modal" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src=imageURL />
          </div>
        </div>
      </div>
    </div>
    <!-- VIDEO -->

  </div>
</template>

<script>
export default {

  data () {
    return {
      tab: 'Mail',
      activeThumbnail: 999,
      isActiveVar: 999,
      itemTab: 0,
      currentPickedBrand: '999',
      imageURL: '',
	  projectOmschrijving: '',
	  projectUrl: '',
	  projectWw: '',
      tablist: [
        {tabname: 'Mail', productname: 'Mails', colorcode: "blue",
          tabmsg: "Een selectie mails die gemaakt zijn voor de dealers van Ponmerken. <br>Opgemaakt met html in tabellen, zodat het compatibel kan zijn met de meeste mailclients.<br><br>Klik hieronder voor beelden.",
          current_img: '1',
          item_images: [
          {item_img: require('@/assets/ARS3983-05-syntec.jpg'), view:'Desk', brand: 'Audi'},
          {item_img: require('@/assets/ARS3983-05-syntec-phone.jpg'), view:'Phone', brand: 'Audi'},
          {item_img: require('@/assets/SEAT136-syntec.jpg'), view:'Desk', brand: 'SEAT'},
          {item_img: require('@/assets/SEAT136-syntec-phone.jpg'), view:'Phone', brand: 'SEAT'},
          {item_img: require('@/assets/BWG804-01-syntec.jpg'), view:'Desk', brand: 'Volkswagen'},
          {item_img: require('@/assets/BWG804-01-syntec-phone.jpg'), view:'Phone', brand: 'Volkswagen'},
          {item_img: require('@/assets/FLEET260-01-syntec.jpg'), view:'Desk', brand: 'ŠKODA'},
          {item_img: require('@/assets/FLEET260-01-syntec-phone.jpg'), view:'Phone', brand: 'ŠKODA'},
          ],
          brand: [
		  {name:'Audi', url:'', description:''},
		  {name:'SEAT', url:'', description:''},
		  {name:'Volkswagen', url:'', description:''},
		  {name:'ŠKODA', url:'', description:''},
		  ],
		  picked_brand: ''
        },
        {tabname: 'Website', productname: 'Websites', colorcode: "blue",
          tabmsg: "Binnen CRC worden de meeste websites opgebouwd in Drupal. Hier een aantal websites waar ik een theme voor heb opgezet.",
          current_img: '1',
          item_images: [
          {item_img: require('@/assets/siena-home.png'), view:'Home', brand: 'Siena D'},
          {item_img: require('@/assets/siena-view.png'), view:'Overzicht', brand: 'Siena D'},
          {item_img: require('@/assets/siena-product.png'), view:'Product', brand: 'Siena D'},
          {item_img: require('@/assets/audisport.png'), view:'Login', brand: 'Audi Sport'},
          {item_img: require('@/assets/audi-on-tour.jpg'), view:'Login', brand: 'Audi on Tour'},
          {item_img: require('@/assets/audi-on-tour-reserveer.jpg'), view:'Reserveer', brand: 'Audi on Tour'},
          {item_img: require('@/assets/audipi-login.png'), view:'Login', brand: 'Audi Pi'},
          {item_img: require('@/assets/audipi-home.png'), view:'Home', brand: 'Audi Pi'},
          {item_img: require('@/assets/audipi-overzicht.png'), view:'Overzicht', brand: 'Audi Pi'},
          {item_img: require('@/assets/audipi-data.png'), view:'Tekst', brand: 'Audi Pi'},
          {item_img: require('@/assets/audipi-menu.png'), view:'Menu', brand: 'Audi Pi'},
          {item_img: require('@/assets/hshoes_home.jpg'), view:'Home', brand: 'Hol* Shit Shoes'},
          {item_img: require('@/assets/hshoes_shoe.jpg'), view:'Schoen', brand: 'Hol* Shit Shoes'},
          {item_img: require('@/assets/hshoes_phpc.jpg'), view:'Club', brand: 'Hol* Shit Shoes'},
          ],
          brand: [
		  {name:'Siena D', url:"http://www.siena-d.com", description:'Webshop Siena D.'},
		  {name:'Audi Sport', url:'', description:'Audi proefrit reserveringsformulier. Dit hangt achter een reserveringssysteem gemaakt in Drupal'},
		  {name:'Audi on Tour', url:'', description:'Audi proefrit reserveringsformulier. Dit hangt achter een reserveringssysteem gemaakt in Drupal'},
		  {name:'Audi Pi', url:'', description:'Webapplicatie voor de telefoon/tablet. Hier wordt (nieuwe) informatie naar Pon medewerkers en dealers gecommuniceerd. Deze applicatie is vergrendeld achter een login.'},
		  {name:'Hol* Shit Shoes', url:"http://www.holyshitshoes.com", description:'Een webshop dat pas opengaat bij goed weer. Vanaf 14 graden celsius.'},
		  ],
		  picked_brand: ''
        },
        {tabname: 'Ontwikkeling', productname: 'Ontwikkeling', colorcode: "blue",
          tabmsg: "In de grijze uren ben ik bezig geweest met het ontwikkelen in vaardigheden die van nut kunnen zijn binnen CRC.",
          current_img: '1',
          item_images: [
          {item_img: require('@/assets/dm-intro.png'), view:'Intro', brand: 'Digitale Magazine'},
          {item_img: require('@/assets/dm-overzicht.png'), view:'Modellen', brand: 'Digitale Magazine'},
          {item_img: require('@/assets/dm-spyder.png'), view:'Audi Spyder', brand: 'Digitale Magazine'},
          {item_img: require('@/assets/dm-kleurkeuze.png'), view:'Kleur kiezen', brand: 'Digitale Magazine'},
          {item_img: require('@/assets/mailsys.jpg'), view:'Preview', brand: 'Mailsysteem'},
          ],
          brand: [
		  {name:'Digitale Magazine', url:"http://audi.demo.crc-it.nl", description:"Opgemaakt in Ruby on Rails. Kan brochures of magazines digitaal weergeven. Het heeft een backend om deze slides aan te maken en invulling te geven."},
		  {name:'Mailsysteem', password:"Bekijk video:" ,url:"https://vimeo.com/265381884", description:"Opgezet met Ruby + Sinatra. Het systeem is opgezet om mails sneller en consistenter op te bouwen. Voorheen deed ik de opbouw van de mails met de hand in een editor."}
		  ],
		  picked_brand: ''
        }
      ]
    }
  },
  methods: {
    enableTab: function(value) {
      this.tab = value;
      this.picked_shoesize = false;
    },
    currentTab: function(value) {
      return this.tab === value;
    },
    activeThumb: function(value) {
      return this.activeThumbnail == value
    },
    activateThumb: function(value) {
      this.activeThumbnail = value
    },
    activeTab: function(value) {
      return this.itemTab == value
    },
    activateTab: function(value) {
      this.itemTab = value
	  this.projectOmschrijving = ''
	  this.projectUrl = ''
	  this.projectWw = ''
    this.currentPickedBrand = '999'
    },
    isActive: function(value) {
      return this.isActiveVar == value
    },
    setActive: function(value) {
      this.isActiveVar = value
    },
    currentBrand: function(value) {
      return this.currentPickedBrand == value
    },
    activateBrand: function(value) {
      this.currentPickedBrand = value
    },
    showScreenshot: function(value) {
      $('#screenshot-modal').modal('show');
      return this.imageURL = value
    },
	showProjectDescription: function(value) {
		return this.projectOmschrijving = value;
	},
	showProjectUrl: function(value) {
		return this.projectUrl = value;
	},
	showProjectWw: function(value) {
		return this.projectWw = value;
	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
.tab-wrapper-inner {
 justify-content: center;
}
.nav-link {
color:#2c3e50;
}
.nav-item.active .nav-link {
  border-color:#dee2e6;
}
.nav.nav-tabs {
  margin: 10px 0;
}
.item-wrapper {
width:48%;
float:right;
}
.item-tab {
  width:100%;
  float:left;
}
.project-url {
	text-decoration:underline;
	color:#2c3e50;
}
.project-url:hover {
	text-decoration:none;
}
.item-details {
  float:left;
  width:100%;
  text-align:left;
  background-color:#F0F0F0;
  padding: 10px 14px;
}
.details-footer-field {
	width:100%;
}
.item-details-description {
	margin-bottom:20px;
}
.item-details-footer {
  float:left;
  width:100%;
  text-align:left;
  padding: 10px 14px;
}
.sub-menu {
  float:left;
  width:100%;
  margin:10px 0;
}
.menu-item {
  border:1px solid black;
  background-color:white;
  color:black;
  float:left;
  padding:0 10px;
  line-height:2;
  cursor:pointer;
  margin-right:10px;
  margin-bottom:10px;
}
.menu-item:hover,
.menu-item.active {
  background-color:black;
  color:white;
}
.item_images {
  width:50%;
  float:left;
}
.big-image {
  height:400px;
  overflow:hidden;
  float:left;
  position:relative;
}
.big-image-icon {
  position:absolute;
  right:10px;
  top:10px;
  background-color:black;
  opacity:0.5;
  border-radius: 4px;
  width:40px;
  height:40px;
  line-height:40px;
  color:white;
  font-size:22px;
}
.big-image:hover {
  cursor:nesw-resize;
  opacity:0.6;
}
.item_images img {
  max-width:100%;
}
.item_images_thumb {
  float:left;
  width:100%;
  padding: 0 0 10px;
  border-bottom: 1px solid black;
  margin:0 0 10px;
}
.item_images_thumb img {
  border:1px solid transparent;
}
.thumb-image {
  max-height:100px;
  float:left;
  width:23.5%;
  margin-right:2%;
  margin-bottom:14px;
  overflow:hidden;
  opacity:0.4;
  cursor: pointer;
}

.thumb-image:hover,
.thumb-image.active {
  /*border:1px solid black;*/
  opacity:1;
}
.thumb-image img {
  width:100%;
}
.item_images_thumb img:nth-child(4n + 4) {
  margin-right:0;
}
.item_images_thumb img:nth-child(4n + 5) {
  clear:left;
}
/* popup */
.modal-dialog {
  max-width:100%;
  width:1200px;
}
#screenshot-modal img {
 max-width:100%;
}
</style>



